import {GET_ALL_BLOGS_LOADING_START, GET_ALL_BLOGS_SUCCESS} from "../types";
import {fetchRequest, mainBlogUrl} from "./fetchTools";


export const getAllBlogs = (type,page) => async (dispatch,getState) => {
    const typesState = type ? `?type=${type}` : ''
    const pageState = page ? `?page=${page}` : ''
    const {data,currPage} = getState().mainBlog
    dispatch({type: GET_ALL_BLOGS_LOADING_START})
    try {
        const fetchData = await fetchRequest(mainBlogUrl + typesState + pageState)
            dispatch({
                type: GET_ALL_BLOGS_SUCCESS,
                payload: {
                    data: page ? [...data,...fetchData.data.data] : fetchData.data.data,
                    type: type ? type + 'Blogs' : 'data',
                    nextPage: !!(fetchData.data.next_page_url),
                    currPage: page ? fetchData.data.current_page : currPage,
                }
            })
    } catch (err) {
        throw new Error(err)
    }
}