import React, {useEffect, useRef, useState} from 'react';
import Slider from "react-slick";

import BlogItem from "../BlogItem/BlogItem";

import styles from './BlogSlider.module.scss'
import SliderDotes from "../SliderDotes/SliderDotes";
import {useDispatch, useSelector} from "react-redux";
import {getAllBlogs} from "../../../redux/action/mainBlog";
import {useLazy} from "../../../hooks/useLazy";

function BlogSlider({smallDistance, type, isFromIndBlog, setIsDataEmpty}) {
    const [isDragging, setIsDragging] = useState(false)
    const sliderRef = useRef(null)
    const dispatch = useDispatch()
    const data = useSelector(state => state.mainBlog[type === 'all' ? 'data' : `${type}Blogs`])
    const {data: indBlogData} = useSelector(state => state.indBlog)
    const [filteredData, setFilteredData] = useState([])
    const {isInView,ref} = useLazy(null,true)

    const settings = {
        dots: true,
        autoplay: true,
        autoplaySpeed: 3000,
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        swipeToSlide: true,
        touchThreshold: 1000,
        arrows: false,
        beforeChange: () => setIsDragging(true),
        afterChange: () => setIsDragging(false),
        customPaging: i => (<SliderDotes
            onClick={() => sliderRef.current.slickGoTo(i)}/>),
        responsive: [
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2,
                }
            },
            {
                breakpoint: 576,
                settings: {
                    slidesToShow: 1,
                    centerMode: true,
                    centerPadding: '0'
                }
            }
        ]
    };

    useEffect(() => {
        if (type && !data?.length && isInView) dispatch(getAllBlogs(type === 'all' ? null : type))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [type, isInView])

    useEffect(() => {
        if (data?.length) {
            setFilteredData(isFromIndBlog ? data.filter(item => item.id !== indBlogData.id).slice(0, 6) : data.slice(0, 6))
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [data])

    useEffect(() => {
        if (setIsDataEmpty) setIsDataEmpty(filteredData?.length)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [filteredData])


    const blogs = filteredData?.length && filteredData.map((item, index) => (
        <div className={styles["blogSlider__item"]} key={index} ref={ref}>
            <BlogItem
                title={item?.title}
                key={item?.id}
                description={item?.description}
                date={item?.date}
                image={item?.image}
                id={item?.id}
                isDragging={isDragging}
            />
        </div>
    ))

    return (
        <section
            className={`${styles['blogSlider']} ${smallDistance ? styles['blogSlider_smDistance'] : styles['blogSlider_lgDistance']} container sliderDotesDef`} ref={ref}>
            {filteredData?.length &&
                <div className={styles["blogSlider__container"]}>
                    {filteredData.length >= 3 ?
                        <Slider {...settings} ref={sliderRef}>
                            {blogs}
                        </Slider> :
                        <div className={styles["blogSlider__wrapper"]}>
                            {blogs}
                        </div>
                    }
                </div>}
        </section>
    );
}

export default BlogSlider;