import React, {memo, useState} from 'react';

import styles from "./BlogItem.module.scss"
import {baseUrl} from "../../../redux/action/fetchTools";
import {loadingLottie} from "../../../assets/lotties";
import Lottie from "lottie-react";
import {Link} from "react-router-dom";
import {blobPagePath} from "../../../router/path";
import {replaceDateFormat, scrollTop} from "../../../globalFunctions";

const BlogItem = memo(({title, description, date, image, id,isDragging}) => {
    const [imgLoaded, setImgLoaded] = useState(false)
    const replacedDate = replaceDateFormat(date)

    const onLoadImg = () => {
        setImgLoaded(true)
    }

    const onClickImg = (e) => {
        isDragging ? e.preventDefault() : scrollTop()

    }
    const blogLink = `${blobPagePath}/${id}`

    return (
        <div className={styles['blogItem']}>
            <Link
                className={styles["blogItem__imgLink"]}
                onClick={onClickImg}
                to={blogLink}>
                <img
                    src={baseUrl + image}
                    alt="blog"
                    className={styles["blogItem__img"]}
                    onLoad={onLoadImg}
                    style={{display: imgLoaded ? 'block' : 'none'}}
                />
            </Link>
            {!imgLoaded &&
                <Lottie
                    animationData={loadingLottie}
                    loop={true}
                    className={styles["blogItem__img"]}
                />}
            <div className={styles["blogItem__textBlock"]}>
                <h5 className={styles["blogItem__title"]}>{title.length >= 64 ? title.slice(0, 64) + '...' : title}</h5>
                <p className={`${styles["blogItem__contentText"]} contentTextDef`}>{description.length >= 150 ? description.slice(0, 150) + '...' : description}</p>
                <div className={styles["blogItem__footBlock"]}>
                    <Link
                        onClick={onClickImg}
                        to={blogLink}
                        className={styles["blogItem__readMoreLink"]}
                    >Read More</Link>
                    <p className={styles["blogItem__date"]}>{replacedDate}</p>
                </div>
            </div>
        </div>
    );
})

export default BlogItem;